<template>
  <div class="user">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block mb20">
        <a download  class="f-small main-c" href="../gy-files/template/用户导入模板.xlsx" >下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload mr15 ml5">批量导入</div>
        </label>
        <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
               @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
        <div @click="$exportDetail('/export/exportUserData.do',{})" class="btn-main mr15">批量导出</div>
        <div @click="removeBatch()" class="btn-main mr15">批量删除</div>
        <div @click="showDialog" class="btn-main-deepen mr15">所属部门</div>
        <div @click="$router.push('/school/user/0')" class="btn-main-deepen">添加用户</div>
      </div>
      <!--<search-box :searchData="searchData" :table="table"></search-box>-->
      <div class="oz-box">
        <el-input @keyup.enter.native="$search(table)" v-model="table.query.keywords" placeholder="姓名/手机号">
          <el-button @click="$search(table)" slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <index-item @changeOrz="changeOrz" :selectedDep.sync="selectedDep" :data="depData"></index-item>
      </div>
      <div class="table-box">
        <resize-table>
          <table class="table">
            <thead>
              <tr>
                <th width="4%"><el-checkbox v-model="checkAll"></el-checkbox></th>
                <th width="8%">姓名</th>
                <th width="11%">职务</th>
                <th width="20%">所属部门</th>
                <th width="14%">角色</th>
                <th width="12%">手机号</th>
                <th width="6%">状态</th>
                <th width="15%">操作</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,key) in table.list">
                <td><el-checkbox v-model="checkboxList[key]"></el-checkbox></td>
                <td>{{item.name}}</td>
                <td>{{item.position}}</td>
                <td>
                  <el-tooltip :content="item.orgList ? item.orgList.map(e=> e.name).join(): '--'" placement="top" effect="dark">
                    <p class="org-list">{{item.orgList ? item.orgList.map(e=> e.name).join(): '--'}}</p>
                  </el-tooltip>
                </td>
                <td>
                  <el-tooltip :content="item.roleList ? item.roleList.map(e=> e.name).join() : '--'" placement="top" effect="dark">
                    <p class="org-list">{{item.roleList ? item.roleList.map(e=> e.name).join() : '--'}}</p>
                  </el-tooltip>
                </td>
                <td>{{item.phone}}</td>
                <td>{{item.enableStatus === 'Enabled'? '启用': '禁用'}}</td>
                <td>
                  <div v-if="item.userId !== 1">
                    <span @click="$router.push(`/school/user/${item.userId}`)" class="oper">编辑</span>
                    <span @click="remove(item)" class="oper">删除</span>
                    <span v-show="item.enableStatus === 'Enabled'" @click="disableUser(item)" class="oper">禁用</span>
                    <span v-show="item.enableStatus === 'Unenabled'" @click="openUser(item)" class="oper">启用</span>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </resize-table>
        <no-data v-show="!table.noData"></no-data>
        <paging :data.sync="table"></paging>
      </div>
      <el-dialog :visible.sync="setDepDialog" class="dialog" width="400px">
        <div v-if="setDepDialog" class="dep-form">
          <p class="title">{{depTitle}}</p>
          <div class="content">
            <item :data="setDepData" :selectedDep.sync="setDepId"></item>
          </div>
          <div class="btns">
            <div @click="setDepDialog = false" class="btn-submit btn-set mr15">关闭</div>
          </div>
        </div>
      </el-dialog>
    </main-part>
  </div>

</template>
<script>
  import indexItem from './IndexItem'
  import item from './Item'
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail],
    components:{
      indexItem,
      item
    },
    data() {
      return {
        nav: [{name: "用户管理", path: ""}],
        searchData: [{type: 'input', placeholder: '用户名'}],
        table: {
          url: '/user/queryUserPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            orgId: null,
            keywords: ''
          }
        },
        // 选择的部门
        selectedDep: [],
        // 表格多选框
        checkboxList:[false],
        // 部门数据
        depData: [],
        // 批量设置所属部门
        setDepData: [],
        // 学校名称
        depTitle: '',
        // 设置所属部门的ID
        setDepId: [],
        // 设置所属部门的名称
        setDepName: [],
        // 选择部门弹窗
        setDepDialog: false
      }
    },
    watch:{
      // 获取所属部门 和角色
      'table.list':function () {
        // this.table.list.forEach((e,i)=>{
        //   let str = []
        //   e.orgList.forEach(n=>{
        //     str.push(n.name)
        //   })
        //   this.table.list[i].orgList = str.join()
        //   let role = []
        //   e.roleList.forEach(n=>{
        //     role.push(n.name)
        //   })
        //   this.table.list[i].roleList = role.join()
        // })
        // console.log(this.table.list)
        this.checkboxList = new Array(this.table.list.length).fill(false)
      }
    },
    computed: {
      checkAll: {
        get: function () {
          if(this.checkboxList.length > 0){
            return this.checkboxList.every(item => item)
          }else{
            return false
          }
        },
        set: function (val) {
          this.checkboxList = new Array(this.table.list.length).fill(val)
        }
      }
    },
    methods: {
      // 展现弹窗
      showDialog(){
        if(this.checkboxList.every(item => !item)){
          this.$message.warning('请选择用户')
          return
        }
        this.setDepDialog = true
      },
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importUser.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if(res.errMsgList && res.errMsgList.length){
          this.$message.error(res.errMsgList[0])
        }else{
          this.$remind('导入成功')
        }
        this.$search(this.table)
      },
      // 设置所属部门
      async setDepFun(){
        let userId = []
          this.checkboxList.forEach((e,i)=>{
          if(e){
            userId.push(this.table.list[i].userId)
          }
        } )
        await this.$ajax({
          url: '/user/setOrgUsersBatch.do',
          el: 'btn-set',
          noText: true,
          query:{
            orgId: this.setDepId,
            userIds: userId
          }
        })
        this.$remind('设置成功')
        this.$search(this.table)
        this.setDepDialog = false
      },
      //
      changeOrz(data){
        this.table.query.orgId = data[0]
        this.$search(this.table)
      },
      // 获取所有的部门
      async getAllDep(){
        let res  = await this.$ajax({
          url: '/org/queryOrganizationTree.do',
          query:{
            orgTypes: ['ADMIN_ORG','TEACH_ORG']
          }
        })
        this.depTitle = res.orgName
        this.depData = this.recursion(res.subOrgList,1)
        this.setDepData = JSON.parse(JSON.stringify(this.depData))
      },
      // 递归给每一个item设置缩进和check-box隐藏
      recursion(arr,i){
        arr.forEach( e => {
          e.tier = i
          e.status = false
          if(e.subOrgList.length > 0){
            this.recursion(e.subOrgList, i + 1)
          }
        })
        return arr
      },
      // 批量删除
      async removeBatch(){
        let status = this.checkboxList.some(e => e)
        if(!status){
          this.$message.warning('请先选择用户')
          return
        }
        await this.$confirm(`您确认删除选中的用户吗？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        let arr = []
        this.checkboxList.forEach((e,i)=>{
          if(e){
            arr.push(this.table.list[i].userId)
          }
        })
        await this.$ajax({
          url: '/user/deleteUserBatch.do',
          query:{
            userIds: arr
          }
        })
        this.$remind('禁用成功')
        this.$search(this.table)
      },
      // 禁用
      async disableUser(item){
        await this.$confirm(`您确认禁用【${item.name}】吗`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/user/disableUser.do',
          query:{
            userId: item.userId
          }
        })
        this.$remind('禁用成功')
        this.$search(this.table)
      },
      // 开启
      async openUser(item){
        await this.$confirm(`您确认启用【${item.name}】吗`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/user/enableUser.do',
          query:{
            userId: item.userId
          }
        })
        this.$remind('启用成功')
        this.$search(this.table)
      },
      // 删除
      async remove(item){
        await this.$confirm(`您确认删除【${item.name}】吗`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/user/deleteUser.do',
          query:{
            userId: item.userId
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      },
    },
    async mounted(){
      await this.$search(this.table)
      this.getAllDep()
      // 取消选择所有的多选框
      this.checkboxList = new Array(this.table.list.length).fill(false)
    }
  }
</script>
<style lang="sass" scoped>
  .user
    /deep/ .el-input-group__append, .el-input-group__prepend
      background: #fff
    .btn-block
      .main-c
        vertical-align: top
        display: inline-block
        line-height: 30px
        margin-top: 2px
    .org-list
      @extend %text-overflow
    .oz-box
      width: 260px
      height: calc(100vh - 220px)
      overflow-y: auto
      border: 1px solid $border
      background: $back-light
      border-radius: 4px
      float: left
      .el-input
        width: calc(100% - 20px)
        margin: 10px
    .table-box
      position: relative
      margin-left: 15px
      width: calc(100% - 275px)
      float: left
    .dialog
      /deep/ .el-dialog
        border-radius: 10px
        background: $back-light
      /deep/ .el-dialog__body
        padding: 0 0 15px 0
      /deep/ .el-dialog__header
        display: none
      .btns
        border-top: 1px solid $border
        text-align: center
        padding-top: 15px
    .dep-form
      border-radius: 10px
      .title
        line-height: 46px
        background: $main-c
        color: #fff
        padding-left: 15px
        border-radius: 10px 10px 0 0
      .content
        width: 100%
        max-height: 560px
        overflow: auto
</style>
