<template>
  <div class="form-item">
    <div v-for="item in treeData">
      <div :class="['item', {'hand': item.subOrgList.length || item.parentOrgId === 1}]"
           :style="{paddingLeft: (item.tier - 1) * 25 + 10 + 'px'}"
           @click="item.status = !item.status">
        <i :style="{'visibility' : item.subOrgList.length ? 'visible': 'hidden'}"
           :class="[{'rotate': item.status}, 'el-icon-caret-right']"></i>
        <el-checkbox @change="changeDep(item)"
                     :value="judge(item.orgId)"
        ><span>{{item.orgName}}</span></el-checkbox>
      </div>

      <el-collapse-transition>
        <item v-show="item.status" @changeOrz="$emit('changeOrz',treeSelectedDep)" :selectedDep="selectedDep"
              :data="item.subOrgList"></item>
      </el-collapse-transition>
    </div>

  </div>
</template>
<script>
  // collapse 展开折叠
  import CollapseTransition from 'element-ui/lib/transitions/collapse-transition'

  export default {
    components: {
      elCollapseTransition: CollapseTransition
    },
    props: ['data', 'selectedDep'],
    name: 'item',
    data () {
      return {}
    },
    computed: {
      'treeData': function () {
        return this.data
      },
      'treeSelectedDep': function () {
        return this.selectedDep
      },
    },
    methods: {
      // 选择或取消选择部门
      changeDep (item) {
        if (this.treeSelectedDep[0] === item.orgId) {
          this.treeSelectedDep.splice(0, 1)
        } else {
          this.treeSelectedDep.splice(0, 1)
          this.treeSelectedDep.push(item.orgId)
        }
        this.$emit('changeOrz', this.treeSelectedDep)
      },
      // 判断是否被选中
      judge (id) {
        return this.treeSelectedDep.indexOf(id) !== -1
      }
    },
    mounted () {
    }
  }
</script>
<style lang="sass" scoped>
  .form-item
    .hand
      cursor: pointer
      background: $back-light !important
    .item
      position: relative
      border-top: 1px solid $border
      line-height: 46px
      padding: 0 10px
      background: #fff
      i
        font-size: $font-large
        margin-right: 6px
        vertical-align: middle
        transition: all .2s
      .rotate
        transform: rotate(90deg)
</style>
